  display: block;\n}\n",".dropdown-content {\n  @extend .z-depth-1;\n  background-color: $dropdown-bg-color;\n  margin: 0;\n  display: none;\n  min-width: 100px;\n  max-height: 650px;\n  overflow-y: auto;\n  opacity: 0;\n  position: absolute;\n  z-index: 999;\n  will-change: width, height;\n\n  li {\n    clear: both;\n    color: $off-black;\n    cursor: pointer;\n    min-height: $dropdown-item-height;\n    line-height: 1.5rem;\n    width: 100%;\n    text-align: left;\n    text-transform: none;\n\n    &:hover, &.active, &.selected {\n      background-color: $dropdown-hover-bg-color;\n    }\n\n    &.active.selected {\n      background-color: darken($dropdown-hover-bg-color, 5%);\n    }\n\n    &.divider {\n      min-height: 0;\n      height: 1px;\n    }\n\n    & > a, & > span {\n      font-size: 16px;\n      color: $dropdown-color;\n      display: block;\n      line-height: 22px;\n      padding: (($dropdown-item-height - 22) / 2) 16px;\n    }\n\n    & > span > label {\n      top: 1px;\n      left: 3px;\n      height: 18px;\n    }\n\n    // Icon alignment override\n    & > a > i {\n      height: inherit;\n      line-height: inherit;\n    }\n  }\n}\n","\n/*!\n * Waves v0.6.0\n * http://fian.my.id/Waves\n *\n * Copyright 2014 Alfiana E. Sibuea and other contributors\n * Released under the MIT license\n * https://github.com/fians/Waves/blob/master/LICENSE\n */\n\n\n.waves-effect {\n    position: relative;\n    cursor: pointer;\n    display: inline-block;\n    overflow: hidden;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    -webkit-tap-highlight-color: transparent;\n    // white-space: nowrap;\n    // outline: 0;\n\n    vertical-align: middle;\n    // cursor: pointer;\n    // border: none;\n    // outline: none;\n    // color: inherit;\n    // background-color: rgba(0, 0, 0, 0);\n    // font-size: 1em;\n    // line-height:1em;\n    // text-align: center;\n    // text-decoration: none;\n    z-index: 1;\n    will-change: opacity, transform;\n    transition: all .3s ease-out;\n\n    .waves-ripple {\n        position: absolute;\n        border-radius: 50%;\n        width: 20px;\n        height: 20px;\n        margin-top:-10px;\n        margin-left:-10px;\n        opacity: 0;\n\n        background: rgba(0,0,0,0.2);\n        // $gradient: rgba(0,0,0,0.2) 0,rgba(0,0,0,.3) 40%,rgba(0,0,0,.4) 50%,rgba(0,0,0,.5) 60%,rgba(255,255,255,0) 70%;\n        // background: -webkit-radial-gradient($gradient);\n        // background: -o-radial-gradient($gradient);\n        // background: -moz-radial-gradient($gradient);\n        // background: radial-gradient($gradient);\n        transition: all 0.7s ease-out;\n        transition-property: transform, opacity;\n        transform: scale(0);\n        pointer-events: none;\n    }\n\n    // Waves Colors\n    &.waves-light .waves-ripple {\n      background-color: rgba(255, 255, 255, 0.45);\n    }\n\n    &.waves-red .waves-ripple {\n      background-color: rgba(244, 67, 54, .70);\n    }\n    &.waves-yellow .waves-ripple {\n      background-color: rgba(255, 235, 59, .70);\n    }\n    &.waves-orange .waves-ripple {\n      background-color: rgba(255, 152, 0, .70);\n    }\n    &.waves-purple .waves-ripple {\n      background-color: rgba(156, 39, 176, 0.70);\n    }\n    &.waves-green .waves-ripple {\n      background-color: rgba(76, 175, 80, 0.70);\n    }\n    &.waves-teal .waves-ripple {\n      background-color: rgba(0, 150, 136, 0.70);\n    }\n\n    // Style input button bug.\n    input[type=\"button\"], input[type=\"reset\"], input[type=\"submit\"] {\n        border: 0;\n        font-style: normal;\n        font-size: inherit;\n        text-transform: inherit;\n        background: none;\n    }\n\n}\n\n.waves-notransition {\n    transition: none #{\"!important\"};\n}\n\n.waves-circle {\n    transform: translateZ(0);\n    -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);\n}\n\n// .waves-button,\n// .waves-button:hover,\