 float: right;\n      margin: 6px 0;\n    }\n  }\n}\n.lean-overlay {\n    position: fixed;\n    z-index:999;\n    top: -100px;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    height: 125%;\n    width: 100%;\n    background: #000;\n    display: none;\n\n    will-change: opacity;\n}\n\n// Modal with fixed action footer\n.modal.modal-fixed-footer {\n  padding: 0;\n  height: 70%;\n\n  .modal-content {\n    position: absolute;\n    height: calc(100% - 56px);\n    max-height: 100%;\n    width: 100%;\n    overflow-y: auto;\n  }\n\n  .modal-footer {\n    border-top: 1px solid rgba(0,0,0,.1);\n    position: absolute;\n    bottom: 0;\n  }\n}\n\n// Modal Bottom Sheet Style\n.modal.bottom-sheet {\n  top: auto;\n  bottom: -100%;\n  margin: 0;\n  width: 100%;\n  max-height: 45%;\n  border-radius: 0;\n  will-change: bottom, opacity;\n}\n",".collapsible {\n  border-top: 1px solid $collapsible-border-color;\n  border-right: 1px solid $collapsible-border-color;\n  border-left: 1px solid $collapsible-border-color;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  @extend .z-depth-1;\n}\n\n.collapsible-header {\n  display: block;\n  cursor: pointer;\n  min-height: $collapsible-height;\n  line-height: $collapsible-height;\n  padding: 0 1rem;\n  background-color: $collapsible-header-color;\n  border-bottom: 1px solid $collapsible-border-color;\n\n  i {\n    width: 2rem;\n    font-size: 1.6rem;\n    line-height: $collapsible-height;\n    display: block;\n    float: left;\n    text-align: center;\n    margin-right: 1rem;\n  }\n}\n\n.collapsible-body {\n  display: none;\n  border-bottom: 1px solid $collapsible-border-color;\n  box-sizing: border-box;\n\n  p {\n    margin: 0;\n    padding: 2rem;\n  }\n}\n\n// sideNav collapsible styling\n.side-nav {\n\n  .collapsible {\n    border: none;\n    box-shadow: none;\n\n    li { padding: 0; }\n  }\n\n  .collapsible-header {\n    background-color: transparent;\n    border: none;\n    line-height: inherit;\n    height: inherit;\n    margin: 0 1rem;\n\n    i { line-height: inherit; }\n  }\n\n  .collapsible-body {\n    border: 0;\n    background-color: $collapsible-header-color;\n\n    li a { margin: 0 1rem 0 2rem; }\n  }\n\n}\n\n// Popout Collapsible\n\n.collapsible.popout {\n  border: none;\n  box-shadow: none;\n  > li {\n    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);\n    // transform: scaleX(.92);\n    margin: 0 24px;\n    transition: margin .35s cubic-bezier(0.250, 0.460, 0.450, 0.940);\n  }\n  > li.active {\n    box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);\n    margin: 16px 0;\n    // transform: scaleX(1);\n  }\n}\n",".chip {\n  display: inline-block;\n  height: 32px;\n  font-size: 13px;\n  font-weight: 500;\n  color: rgba(0,0,0,.6);\n  line-height: 32px;\n  padding: 0 12px;\n  border-radius: 16px;\n  background-color: $chip-bg-color;\n\n  img {\n    float: left;\n    margin: 0 8px 0 -12px;\n    height: 32px;\n    width: 32px;\n    border-radius: 50%;\n  }\n\n  i.material-icons {\n    cursor: pointer;\n    float: right;\n    font-size: 16px;\n    line-height: 32px;\n    padding-left: 8px;\n  }\n}",".materialboxed {\n  display: block;\n  cursor: zoom-in;\n  position: relative;\n  transition: opacity .4s;\n\n  &:hover {\n    &:not(.active) {\n      opacity: .8;\n    }\n    will-change: left, top, width, height;\n  }\n}\n\n.materialboxed.active {\n  cursor: zoom-out;\n}\n\n#materialbox-overlay {\n  position:fixed;\n  top:0;\n  left:0;\n  right: 0;\n  bottom: 0;\n  background-color: #292929;\n  z-index: 999;\n\n  will-change: opacity;\n}\n.materialbox-caption {\n  position: fixed;\n  display: none;\n  color: #fff;\n  line-height: 50px;\n  bottom: 0;\n  width: 100%;\n  text-align: center;\n  padding: 0% 15%;\n  height: 50px;\n  z-index: 1000;\n  -webkit-font-smoothing: antialiased;\n}","/* Remove Focus Boxes */\nselect:focus {\n  outline: 1px solid lighten($secondary-color, 47%);\n}\nbutton:focus {\n  outline: none;\n  background-color: lighten($button-color, 4%);\n}\n\nlabel {\n  font-size: $label-font-size;\n  color: $input-border-color;\n}\n\n/***************************\n   Text Inputs + Textarea\n****************************/\n\n// Style Placeholders\n::-webkit-input-placeholder {\n   color: lighten($input-border-color, 20%);\n}\n\n:-moz-placeholder { /* Firefox 18- */\n   color: lighten($input-border-color, 20%);\n}\n\n::-moz-placeholder {  /* Firefox 19+ */\n   color: lighten($input-border-color, 20%);\n}\n\n:-ms-input-placeholder {\n   color: lighten($input-border-color, 20%);\n}\n\n// Text inputs\ninput:not([type]),\ninput[type=text],\ninput[type=password],\ninput[type=email],\ninput[type=url],\ninput[type=time],\ninput[type=date],\ninput[type=datetime-local],\ninput[type=tel],\ninput[type=number],\ninput[type=search],\ntextarea.materialize-textarea {\n\n  // General Styles\n  background-color: transparent;\n  border: none;\n  border-bottom: 1px solid $input-border-color;\n  border-radius: 0;\n  outline: none;\n  height: 3rem;\n  width: 100%;\n\n  font-size: 1rem;\n  margin: 0 0 15px 0;\n  padding: 0;\n  box-shadow: none;\n  box-sizing: content-box;\n  transition: all .3s;\n\n\n  // Disabled input style\n  &:disabled, &[readonly=\"readonly\"] {\n    color: $input-disabled-color;\n    border-bottom: 1px dotted $input-disabled-color;\n  }\n  // Disabled label style\n  &:disabled+label, &[readonly=\"readonly\"]+label {\n    color: $input-disabled-color;\n  }\n  // Focused input style\n  &:focus:not([readonly]) {\n    border-bottom: 1px solid $input-focus-color;\n    box-shadow: 0 1px 0 0 $input-focus-color;\n  }\n  // Focused label style\n  &:focus:not([readonly])+label {\n    color: $input-focus-color;\n  }\n  // Valid Input Style\n  &.valid,\n  &:focus.valid {\n    border-bottom: 1px solid $input-success-color;\n    box-shadow: 0 1px 0 0 $input-success-color;\n  }\n  // Custom Success Message\n  &.valid + label:after,\n  &:focus.valid + label:after {\n    content: attr(data-success);\n    color: $input-success-color;\n    opacity: 1;\n  }\n  // Invalid Input Style\n  &.invalid,\n  &:focus.invalid {\n    border-bottom: 1px solid $input-error-color;\n    box-shadow: 0 1px 0 0 $input-error-color;\n  }\n  // Custom Error message\n  &.invalid + label:after,\n  &:focus.invalid + label:after {\n    content: attr(data-error);\n    color: $input-error-color;\n    opacity: 1;\n  }\n\n  // Form Message Shared Styles\n  & + label:after {\n    display: block;\n    content: \"\";\n    position: absolute;\n    top: 65px;\n    opacity: 0;\n    transition: .2s opacity ease-out, .2s color ease-out;\n  }\n}\n\n// Styling for input field wrapper\n.input-field {\n  position: relative;\n  margin-top: 1rem;\n\n  label {\n    color: $input-border-color;\n    position: absolute;\n    top: 0.8rem;\n    left: $gutter-width / 2;\n    font-size: 1rem;\n    cursor: text;\n    transition: .2s ease-out;\n  }\n  label.active {\n    font-size: $label-font-size;\n    transform: translateY(-140%);\n  }\n\n  // Prefix Icons\n  .prefix {\n    position: absolute;\n    width: 3rem;\n    font-size: 2rem;\n    transition: color .2s;\n\n    &.active { color: $input-focus-color; }\n  }\n  .prefix ~ input,\n  .prefix ~ textarea {\n    margin-left: 3rem;\n    width: 92%;\n    width: calc(100% - 3rem);\n  }\n  .prefix ~ textarea { padding-top: .8rem; }\n  .prefix ~ label { margin-left: 3rem; }\n\n  @media #{$medium-and-down} {\n    .prefix ~ input {\n      width: 86%;\n      width: calc(100% - 3rem);\n    }\n  }\n  @media #{$small-and-down} {\n    .prefix ~ input {\n      width: 80%;\n      width: calc(100% - 3rem);\n    }\n  }\n}\n\n// Search Field\n.input-field input[type=search] {\n  display: block;\n  line-height: inherit;\n  padding-left: 4rem;\n  width: calc(100% - 4rem);\n\n  &:focus {\n    background-color: $input-bg-color;\n    border: 0;\n    box-shadow: none;\n    color: #444;\n\n    & + label i,\n    & ~ .mdi-navigation-close,\n    & ~ .material-icons {\n      color: #444;\n    }\n  }\n\n  & + label {\n    left: 1rem;\n  }\n\n  & ~ .mdi-navigation-close,\n  & ~ .material-icons {\n    position: absolute;\n    top: 0;\n    right: 1rem;\n    color: transparent;\n    cursor: pointer;\n    font-size: 2rem;\n    transition: .3s color;\n  }\n}\n\n\n// Default textarea\ntextarea {\n   width: 100%;\n   height: 3rem;\n   background-color: transparent;\n\n  &.materialize-textarea {\n    overflow-y: hidden; /* prevents scroll bar flash */\n    padding: 1.6rem 0; /* prevents text jump on Enter keypress */\n    resize: none;\n    min-height: 3rem;\n  }\n}\n\n// For textarea autoresize\n.hiddendiv {\n  display: none;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */\n  padding-top: 1.2rem; /* prevents text jump on Enter keypress */\n}\n\n\n/***************\n  Radio Buttons\n***************/\n\n/* Remove default Radio Buttons */\n[type=\"radio\"]:not(:checked),\n[type=\"radio\"]:checked {\n  position: absolute;\n  left: -9999px;\n  visibility: hidden;\n}\n\n[type=\"radio\"]:not(:checked) + label,\n[type=\"radio\"]:checked + label {\n  position: relative;\n  padding-left: 35px;\n  cursor: pointer;\n  display: inline-block;\n  height: 25px;\n  line-height: 25px;\n  font-size: 1rem;\n  transition: .28s ease;\n\n  -khtml-user-select: none; /* webkit (konqueror) browsers */\n  user-select: none;\n}\n\n[type=\"radio\"] + label:before,\n[type=\"radio\"] + label:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  margin: 4px;\n  width: 16px;\n  height: 16px;\n  z-index: 0;\n  transition: .28s ease;\n}\n\n/* Unchecked styles */\n[type=\"radio\"]:not(:checked) + label:before {\n  border-radius: 50%;\n  border: 2px solid $radio-empty-color;\n}\n[type=\"radio\"]:not(:checked) + label:after {\n  border-radius: 50%;\n  border: 2px solid $radio-empty-color;\n  z-index: -1;\n\n  transform: scale(0);\n}\n\n/* Checked styles */\n[type=\"radio\"]:checked + label:before {\n  border-radius: 50%;\n  border: 2px solid transparent;\n}\n[type=\"radio\"]:checked + label:after {\n  border-radius: 50%;\n  border: 2px solid $radio-fill-color;\n  background-color: $radio-fill-color;\n  z-index: 0;\n  transform: scale(1.02);\n}\n\n/* Radio With gap */\n[type=\"radio\"].with-gap:checked + label:before {\n  border-radius: 50%;\n  border: 2px solid $radio-fill-color;\n}\n[type=\"radio\"].with-gap:checked + label:after {\n  border-radius: 50%;\n  border: 2px solid $radio-fill-color;\n  background-color: $radio-fill-color;\n  z-index: 0;\n  transform: scale(.5);\n}\n\n/* Disabled Radio With gap */\n[type=\"radio\"].with-gap:disabled:checked + label:before {\n  border: 2px solid $input-disabled-color;\n}\n[type=\"radio\"].with-gap:disabled:checked + label:after {\n  border: none;\n  background-color: $input-disabled-color;\n}\n\n/* Disabled style */\n[type=\"radio\"]:disabled:not(:checked) + label:before,\n[type=\"radio\"]:disabled:checked + label:before {\n  background-color: transparent;\n  border-color: $input-disabled-color;\n}\n[type=\"radio\"]:disabled + label {\n  color: $input-disabled-color;\n}\n[type=\"radio\"]:disabled:not(:checked) + label:before {\n  border-color: $input-disabled-color;\n}\n[type=\"radio\"]:disabled:checked + label:after {\n  background-color: $input-disabled-color;\n  border-color: $input-disabled-solid-color;\n}\n\n/***************\n   Checkboxes\n***************/\n\n/* CUSTOM CSS CHECKBOXES */\nform p {\n  margin-bottom: 10px;\n  text-align: left;\n}\nform p:last-child {\n  margin-bottom: 0;\n}\n\n/* Remove default checkbox */\n[type=\"checkbox\"]:not(:checked),\n[type=\"checkbox\"]:checked {\n  position: absolute;\n  left: -9999px;\n  visibility: hidden;\n}\n\n// Checkbox Styles\n[type=\"checkbox\"] {\n\n  // Text Label Style\n  + label {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n\n    -webkit-user-select: none; /* webkit (safari, chrome) browsers */\n    -moz-user-select: none; /* mozilla browsers */\n    -khtml-user-select: none; /* webkit (konqueror) browsers */\n    -ms-user-select: none; /* IE10+ */\n  }\n\n  /* checkbox aspect */\n  + label:before {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 2px;\n    transition: .2s;\n  }\n\n  &:not(:checked):disabled + label:before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + label:before {\n    top: -4px;\n    left: -3px;\n    width: 12px; height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: 2px solid $radio-fill-color;\n    border-bottom: 2px solid $radio-fill-color;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + label:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  +label:before {\n    left: -10px;\n    top: -11px;\n    width: 10px; height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: 2px solid $radio-fill-color;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + label:before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + label:after {\n    border-radius: 2px;\n  }\n  + label:before,\n  + label:after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + label:before {\n    width: 0;\n     height: 0;\n     border: 3px solid transparent;\n     left: 6px;\n     top: 10px;\n\n     -webkit-transform: rotateZ(37deg);\n             transform: rotateZ(37deg);\n     -webkit-transform-origin: 20% 40%;\n             transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + label:after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + label:before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-bg-color;\n      border-bottom: 2px solid $input-bg-color;\n      -webkit-transform: rotateZ(37deg);\n      transform: rotateZ(37deg);\n\n      -webkit-transform-origin: 100% 100%;\n      transform-origin: 100% 100%;\n    }\n\n    + label:after {\n      top: 0px;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n  // Disabled style\n  &:disabled:not(:checked) + label:before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + label:after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + label:before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + label:after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n\n/***************\n     Switch\n***************/\n.switch,\n.switch * {\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -khtml-user-select: none;\n    -ms-user-select: none;\n}\n\n.switch label {\n    cursor: pointer;\n}\n\n.switch label input[type=checkbox]{\n    opacity: 0;\n    width: 0;\n    height: 0;\n}\n\n.switch label input[type=checkbox]:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n}\n\n.switch label input[type=checkbox]:checked + .lever:after {\n    background-color: $switch-bg-color;\n}\n\n.switch label .lever {\n    content: \"\";\n    display: inline-block;\n    position: relative;\n    width: 40px;\n    height: 15px;\n    background-color: $switch-unchecked-lever-bg;\n    border-radius: 15px;\n    margin-right: 10px;\n    transition: background 0.3s ease;\n    vertical-align: middle;\n    margin: 0 16px;\n}\n\n.switch label .lever:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 21px;\n    height: 21px;\n    background-color: $switch-unchecked-bg;\n    border-radius: 21px;\n    box-shadow: 0 1px 3px 1px rgba(0,0,0,.4);\n    left: -5px;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease;\n}\n\n// Switch active style\ninput[type=checkbox]:checked:not(:disabled) ~ .lever:active:after {\n       box-shadow: 0 1px 3px 1px rgba(0,0,0,.4), 0 0 0 15px transparentize($switch-bg-color, .9);\n}\n\ninput[type=checkbox]:not(:disabled) ~ .lever:active:after {\n       box-shadow: 0 1px 3px 1px rgba(0,0,0,.4), 0 0 0 15px rgba(0, 0, 0, .08);\n}\n\n.switch label input[type=checkbox]:checked + .lever:after {\n    left: 24px;\n}\n\n// Disabled Styles\n.switch input[type=checkbox][disabled] + .lever{\n  cursor: default;\n}\n.switch label input[type=checkbox][disabled] + .lever:after,\n.switch label input[type=checkbox][disabled]:checked + .lever:after {\n    background-color: $input-disabled-solid-color;\n}\n\n/***************\n  Select Field\n***************/\n.select-label {\n  position: absolute;\n}\n\n.select-wrapper {\n  position: relative;\n\n  input.select-dropdown {\n    position: relative;\n    cursor: pointer;\n    // color: #444;\n    background-color: transparent;\n    border: none;\n    border-bottom: 1px solid $input-border-color;\n    outline: none;\n    height: 3rem;\n    line-height: 3rem;\n    width: 100%;\n    font-size: 1rem;\n    margin: 0 0 15px 0;\n    padding: 0;\n    display: block;\n  }\n\n  span.caret {\n    color: initial;\n    position: absolute;\n    right: 0;\n    top: 16px;\n    font-size: 10px;\n    &.disabled {\n      color: $input-disabled-color;\n    }\n  }\n\n  & + label {\n    position: absolute;\n    top: -14px;\n    font-size: $label-font-size;\n  }\n}\n\nselect { display: none; }\nselect.browser-default { display: block; }\n\n// Disabled styles\nselect:disabled {\n  color: rgba(0,0,0,.3);\n}\n\n.select-wrapper input.select-dropdown:disabled {\n  color: rgba(0,0,0,.3);\n  cursor: default;\n  -webkit-user-select: none; /* webkit (safari, chrome) browsers */\n  -moz-user-select: none; /* mozilla browsers */\n  -ms-user-select: none; /* IE10+ */\n  border-bottom: 1px solid rgba(0,0,0,.3);\n}\n\n.select-wrapper i {\n  color: rgba(0,0,0,.3);\n}\n\n.select-dropdown li.disabled,\n.select-dropdown li.disabled > span,\n.select-dropdown li.optgroup {\n  color: rgba(0,0,0,.3);\n  background-color: transparent;\n}\n\n// Icons\n.select-dropdown li {\n  img {\n    height: $dropdown-item-height - 10;\n    width: $dropdown-item-height - 10;\n    margin: 5px 15px;\n    float: right;\n  }\n}\n\n// Optgroup styles\n.select-dropdown li.optgroup {\n  border-top: 1px solid $dropdown-hover-bg-color;\n\n  &.selected > span {\n    color: rgba(0, 0, 0, .7);\n  }\n\n  & > span {\n    color: rgba(0, 0, 0, .4);\n  }\n\n  & ~ li:not(.optgroup) {\n    padding-left: 1rem;\n  }\n}\n\n/*********************\n      File Input\n**********************/\n.file-field {\n  position: relative;\n\n  .file-path-wrapper {\n    overflow: hidden;\n    padding-left: 10px;\n  }\n\n  input.file-path { width: 100%; }\n\n  .btn {\n    float: left;\n    height: 3rem;\n    line-height: 3rem;\n  }\n\n  span {\n    cursor: pointer;\n  }\n\n  input[type=file] {\n    position: absolute;\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    font-size: 20px;\n    cursor: pointer;\n    opacity: 0;\n    filter: alpha(opacity=0);\n  }\n}\n\n/***************\n      Range\n***************/\n\n.range-field {\n  position: relative;\n}\n\ninput[type=range], input[type=range] + .thumb {\n  @extend .no-select;\n  cursor: pointer;\n}\n\ninput[type=range] {\n  position: relative;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  width: 100%;\n  margin: 15px 0px;\n  padding: 0;\n}\ninput[type=range] + .thumb {\n  position: absolute;\n  border: none;\n  height: 0;\n  width: 0;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  top: 10px;\n  margin-left: -6px;\n\n  transform-origin: 50% 50%;\n  transform: rotate(-45deg);\n\n  .value {\n    display: block;\n    width: 30px;\n    text-align: center;\n    color: $radio-fill-color;\n    font-size: 0;\n    transform: rotate(45deg);\n  }\n\n  &.active {\n    border-radius: 50% 50% 50% 0;\n\n    .value {\n      color: $input-bg-color;\n      margin-left: -1px;\n      margin-top: 8px;\n      font-size: 10px;\n    }\n  }\n}\n\n\ninput[type=range]:focus {\n  outline: none;\n}\n\n// WebKit\ninput[type=range]{\n  -webkit-appearance: none;\n}\n\ninput[type=range]::-webkit-slider-runnable-track {\n  height: 3px;\n  background: #c2c0c2;\n  border: none;\n}\n\ninput[type=range]::-webkit-slider-thumb {\n  -webkit-appearance: none;\n  border: none;\n  height: 14px;\n  width: 14px;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  transform-origin: 50% 50%;\n  margin: -5px 0 0 0;\n  transition: .3s;\n}\n\ninput[type=range]:focus::-webkit-slider-runnable-track {\n  background: #ccc;\n}\n\n// FireFox\ninput[type=range]{\n  /* fix for FF unable to apply focus style bug  */\n  border: 1px solid white;\n\n  /*required for proper track sizing in FF*/\n}\n\ninput[type=range]::-moz-range-track {\n  height: 3px;\n  background: #ddd;\n  border: none;\n}\n\ninput[type=range]::-moz-range-thumb {\n  border: none;\n  height: 14px;\n  width: 14px;\n  border-radius: 50%;\n  background: $radio-fill-color;\n  margin-top: -5px;\n}\n\n/*hide the outline behind the border*/\ninput[type=range]:-moz-focusring{\n  outline: 1px solid white;\n  outline-offset: -1px;\n}\n\ninput[type=range]:focus::-moz-range-track {\n  background: #ccc;\n}\n\n// IE 10+\ninput[type=range]::-ms-track {\n  height: 3px;\n\n  /*remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead */\n  background: transparent;\n\n  /*leave room for the larger thumb to overflow with a transparent border */\n  border-color: transparent;\n  border-width: 6px 0;\n\n  /*remove default tick marks*/\n  color: transparent;\n}\n\ninput[type=range]::-ms-fill-lower {\n  background: #777;\n}\n\ninput[type=range]::-ms-fill-upper {\n  background: #ddd;\n}\n\ninput[type=range]::-ms-thumb {\n  border: none;\n  height: 14px;\n  width: 14px;\n  border-radius: 50%;\n  background: $radio-fill-color;\n}\n\ninput[type=range]:focus::-ms-fill-lower {\n  background: #888;\n}\n\ninput[type=range]:focus::-ms-fill-upper {\n  background: #ccc;\n}\n\n/***************************\n   Text Inputs + Textarea\n****************************/\nselect {\n   background-color: rgba(255, 255, 255, 0.90);\n   width: 100%;\n   padding: 5px;\n   border: 1px solid #f2f2f2;\n   border-radius: 2px;\n   height: 3rem;\n}\n","/***************\n    Nav List\n***************/\n.table-of-contents {\n  &.fixed {\n      position: fixed;\n    }\n\n  li {\n    padding: 2px 0;\n  }\n  a {\n    display: inline-block;\n    font-weight: 300;\n    color: #757575;\n    padding-left: 20px;\n    height: 1.5rem;\n    line-height: 1.5rem;\n    letter-spacing: .4;\n    display: inline-block;\n\n    &:hover {\n      color: lighten(#757575, 20%);\n      padding-left: 19px;\n      border-left: 1px solid lighten(color(\"materialize-red\", \"base\"),10%);\n    }\n    &.active {\n      font-weight: 500;\n      padding-left: 18px;\n      border-left: 2px solid lighten(color(\"materialize-red\", \"base\"),10%);\n    }\n  }\n}\n",".side-nav {\n  position: fixed;\n  width: 240px;\n  left: -105%;\n  top: 0;\n  margin: 0;\n  height: 100%;\n  height: calc(100% + 60px);\n  height: -moz-calc(100%); //Temporary Firefox Fix\n  padding-bottom: 60px;\n  background-color: $sidenav-bg-color;\n  z-index: 999;\n  overflow-y: auto;\n\n  @extend .z-depth-1;\n  will-change: left;\n\n  // Right Align\n  &.right-aligned {\n    will-change: right;\n    right: -105%;\n    left: auto;\n  }\n\n  .collapsible{\n    margin: 0;\n  }\n\n\n  li {\n    float: none;\n    padding: 0 $sidenav-padding-right;\n    line-height: $sidenav-item-height;\n    &:hover, &.active { background-color: #ddd; }\n  }\n  a {\n    color: #444;\n    display: block;\n    font-size: 1rem;\n    height: $sidenav-item-height;\n    line-height: $sidenav-item-height;\n    padding: 0 $sidenav-padding-right;\n  }\n}\n\n\n// Touch interaction\n.drag-target {\n  height: 100%;\n  width: 10px;\n  position: fixed;\n  top: 0;\n  z-index: 998;\n}\n\n\n// Hidden side-nav for all sizes\n.side-nav.fixed {\n  a {\n    display: block;\n    padding: 0 $sidenav-padding-right;\n    color: #444;\n  }\n}\n\n\n// Fixed side-nav shown\n.side-nav.fixed {\n  left: 0;\n  position: fixed;\n\n  // Right Align\n  &.right-aligned {\n    right: 0;\n    left: auto;\n  }\n}\n\n// Fixed sideNav hide on smaller\n@media #{$medium-and-down} {\n  .side-nav.fixed {\n    left: -105%;\n\n    &.right-aligned {\n      right: -105%;\n      left: auto;\n    }\n  }\n}\n\n\n.side-nav .collapsible-body li.active,\n.side-nav.fixed .collapsible-body li.active {\n  background-color: $primary-color;\n  a {\n    color: $sidenav-bg-color;\n  }\n}\n\n\n#sidenav-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n\n  height: 120vh;\n  background-color: rgba(0,0,0,.5);\n 